              @click="showDeleteConfirm = false" 
              class="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
            >
              Hy
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, reactive, onMounted } from 'vue'
import AdminHeader from '@/components/admin/AdminHeader.vue'
import api from '@/api'
import notificationService from '@/utils/notificationService'

// State
const products = ref([])
const categories = ref([])
const isLoading = ref(true)
const error = ref(null)

// Pagination
const currentPage = ref(0)
const pageSize = ref(10)
const totalItems = ref(0)
const totalPages = ref(0)

// Search filters
const searchTerm = ref('')
const selectedCategory = ref('')
const stockFilter = ref('all')
const statusFilter = ref('')

// Modal state
const isModalOpen = ref(false)
const isEditMode = ref(false)
const currentProductId = ref(null)

// Product form
const productForm = reactive({
  name: '',
  slug: '',
  description: '',
  price: 0,
  originalPrice: 0,
  image: '',
  categoryId: null,
  stock: 0,
  status: 'ACTIVE'
})

// State for quick updates
const isUpdating = ref({})

// Computed properties
const filteredProducts = computed(() => {
  let result = products.value

  // Filter by search term
    if (searchTerm.value) {
    const search = searchTerm.value.toLowerCase()
    result = result.filter(product => 
      product.name.toLowerCase().includes(search) || 
      (product.description && product.description.toLowerCase().includes(search))
    )
  }

  // Filter by category
  if (selectedCategory.value) {
    result = result.filter(product => String(product.categoryId) === String(selectedCategory.value))
  }

  // Filter by stock
  if (stockFilter.value === 'in-stock') {
    result = result.filter(product => product.stock > 0)
  } else if (stockFilter.value === 'out-of-stock') {
    result = result.filter(product => product.stock <= 0)
  }

  // Filter by status
  if (statusFilter.value) {
    result = result.filter(product => product.status === statusFilter.value)
  }

  return result
})

// Convert string to slug
function slugify(text) {
  return text
    .toString()
    .toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .replace(//g, 'd')
    .replace(/[^\w\s-]/g, '')
    .replace(/[\s_-]+/g, '-')
    .replace(/^-+|-+$/g, '')
}

// Auto-generate slug when name changes
function updateSlug() {
  if (!isEditMode.value || (isEditMode.value && !productForm.slug)) {
    productForm.slug = slugify(productForm.name)
  }
}

// Format price
function formatPrice(price) {
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND',
    maximumFractionDigits: 0
  }).format(price)
}

// Status display
function getStatusDisplay(product) {
  if (product.status === 'ACTIVE') {
    return { text: 'ang bn', class: 'bg-green-100 text-green-800' }
  }
  return { text: 'Khng bn', class: 'bg-gray-100 text-gray-800' }
}

// Hm ly tn danh mc theo id
function getCategoryName(categoryId) {
  const category = categories.value.find(c => c.id === categoryId)
  return category ? category.name : 'Cha phn loi'
}

// Hm xc nh trng thi sn phm
function getProductStatus(product) {
  // Nu trng thi khng hp l hoc l OUT_OF_STOCK, chuyn v INACTIVE
  if (!product.status || product.status === 'OUT_OF_STOCK' || !['ACTIVE', 'INACTIVE'].includes(product.status)) {
    return 'INACTIVE';
  }
  return product.status;
}

// API calls
async function fetchCategories() {
  try {
    console.log('ang ti danh mc sn phm...')
    
    try {
      // Th gi API danh mc
      const response = await api.category.getCategories()
      if (response.data && response.data.length > 0) {
        console.log(' ti danh mc t API:', response.data)
        categories.value = response.data
  } else {
        console.log('API tr v danh mc rng')
        categories.value = []
      }
    } catch (apiError) {
      console.error('Li khi ti danh mc t API:', apiError)
      categories.value = []
    }
  } catch (error) {
    console.error('Li chung khi ti danh mc:', error)
    categories.value = []
  }
}

function redirectToLogin() {
  window.location.href = '/admin/login'
}

// Cp nht hm fetchAllProducts da theo API mi
async function fetchAllProducts() {
  isLoading.value = true;
  error.value = null;
  
  try {
    console.log('=== Bt u ti sn phm ===');
    
    // Kim tra token trc khi gi API
    const token = localStorage.getItem('admin_current_token') || localStorage.getItem('token');
    if (!token) {
      error.value = 'Bn cha ng nhp hoc phin lm vic  ht hn. Vui lng ng nhp li.';
      isLoading.value = false;
      setTimeout(() => { redirectToLogin(); }, 3000);
      return;
    }
    
    // Thit lp params - s lng ln  ly tt c sn phm
    const params = {
      page: 0,
      size: 10000 // S lng ln  m bo ti tt c sn phm
    };
    
    // Ch thm tham s status khi ngi dng chn lc theo trng thi c th
    if (statusFilter.value) {
      params.status = statusFilter.value;
    }
    
    console.log('ang ti sn phm vi params:', params);
    
    // S dng axios trc tip
    const axios = (await import('axios')).default;
    const response = await axios.get('/products', {
      baseURL: 'http://localhost:8080/api',
      params,
      headers: {
        'Content-Type': 'application/json',
        'Authorization': token ? `Bearer ${token}` : undefined
      }
    });
    
    console.log('API tr v:', response.data);
    
    // Kim tra cu trc v x l d liu t API
    if (response.data) {
      let apiProducts = [];
      
      // Kim tra cu trc phn hi t API
      if (response.data.content && Array.isArray(response.data.content)) {
        // Cu trc ph bin: { content: [...], totalElements, totalPages }
        apiProducts = response.data.content;
        totalItems.value = response.data.totalElements || apiProducts.length;
        totalPages.value = response.data.totalPages || 1;
      } else if (response.data.products && Array.isArray(response.data.products)) {
        // Cu trc thay th: { products: [...], totalItems, totalPages }
        apiProducts = response.data.products;
        totalItems.value = response.data.totalItems || apiProducts.length;
        totalPages.value = response.data.totalPages || 1;
      } else if (Array.isArray(response.data)) {
        // Mng trc tip khng c phn trang
        apiProducts = response.data;
        totalItems.value = apiProducts.length;
        totalPages.value = 1;
      }
      
      console.log(` ti ${apiProducts.length} sn phm`);
      
      if (apiProducts.length > 0) {
        // Cp nht danh sch sn phm
        products.value = apiProducts.map(p => ({
          id: p.id,
          name: p.name,
          description: p.description || '',
          price: p.price,
          originalPrice: p.originalPrice,
          image: p.image || '/assets/images/products/sam-tuoi.png',
          categoryId: p.categoryId,
          categoryName: getCategoryName(p.categoryId),
          stock: p.stock || 0,
          status: p.status || 'INACTIVE',
          slug: p.slug || slugify(p.name)
        }));
        
        console.log(` ti tng cng ${products.value.length} sn phm t API`);
      } else {
        products.value = [];
        console.log('Khng c sn phm no c ti');
      }
    } else {
      products.value = [];
      totalItems.value = 0;
      totalPages.value = 0;
      console.log('Khng c d liu t API');
    }
  } catch (err) {
    console.error('Li khi ti sn phm:', err);
    error.value = 'Khng th ti danh sch sn phm. Vui lng th li sau.';
    products.value = [];
    totalItems.value = 0;
    totalPages.value = 0;
  } finally {
    isLoading.value = false;
  }
}

// Modal functions
function openAddProductModal() {
  isEditMode.value = false
  currentProductId.value = null
  
  // Reset form
    Object.assign(productForm, {
      name: '',
    slug: '',
    description: '',
      price: 0,
    originalPrice: 0,
    image: '',
    categoryId: categories.value.length > 0 ? categories.value[0].id : null,
      stock: 0,
    status: 'ACTIVE'
  })
  
  isModalOpen.value = true
}

function openEditProductModal(product) {
  isEditMode.value = true
  currentProductId.value = product.id
  
  // Populate form with product data
  Object.assign(productForm, {
    name: product.name,
    slug: product.slug,
    description: product.description,
    price: product.price,
    originalPrice: product.originalPrice || product.price,
    image: product.image,
    categoryId: product.categoryId,
    stock: product.stock,
    status: product.status
  })
  
  isModalOpen.value = true
}

function closeModal() {
  isModalOpen.value = false
}

// Save product
async function saveProduct() {
  if (!productForm.name || !productForm.price) {
    notificationService.show('Vui lng in y  thng tin sn phm', {
      title: 'Li',
      type: 'error'
    })
    return
  }
  
  isLoading.value = true
  
  try {
    // Kim tra token trc khi gi API
    const token = localStorage.getItem('admin_current_token') || localStorage.getItem('token')
    if (!token) {
      notificationService.show('Bn cha ng nhp hoc phin lm vic  ht hn. Vui lng ng nhp li.', {
        title: 'Li xc thc',
        type: 'error'
      })
      isLoading.value = false
      // T ng chuyn hng n trang ng nhp sau 3 giy
      setTimeout(() => {
        redirectToLogin()
      }, 3000)
      return
    }
    
    // Chuyn i gi v stock thnh s
    const productData = {
      ...productForm,
      price: Number(productForm.price),
      originalPrice: productForm.originalPrice ? Number(productForm.originalPrice) : null,
      stock: Number(productForm.stock),
    }
    
    try {
      let response
      
      if (isEditMode.value) {
        console.log(`ang cp nht sn phm ID: ${currentProductId.value}`)
        
        // S dng API wrapper
        response = await api.product.updateProduct(currentProductId.value, productData)
        
        console.log('Sn phm  c cp nht:', response.data)
        
        // Cp nht sn phm trong danh sch
        const index = products.value.findIndex(p => p.id === response.data.id)
    if (index !== -1) {
          // m bo trng thi hp l khi cp nht li danh sch
          products.value[index] = {
            ...response.data,
            categoryName: getCategoryName(response.data.categoryId)
          }
        }
        
        notificationService.show('Sn phm  c cp nht thnh cng', {
          title: 'Thnh cng',
          type: 'success'
        })
  } else {
        console.log('ang to sn phm mi')
        
        // S dng API wrapper
        response = await api.product.createProduct(productData)
        
        console.log('Sn phm mi  c to:', response.data)
        
        // Thm sn phm mi vo danh sch
        products.value.unshift({
          ...response.data,
          categoryName: getCategoryName(response.data.categoryId)
        })
        
        notificationService.show('Sn phm mi  c to thnh cng', {
          title: 'Thnh cng',
          type: 'success'
        })
      }
      
      // ng modal sau khi lu
  closeModal()
      
    } catch (apiError) {
      console.error('Li khi gi API sn phm:', apiError)
      
      if (apiError.response) {
        if (apiError.response.status === 401 || apiError.response.status === 403) {
          notificationService.show('Token khng hp l hoc  ht hn. Vui lng ng nhp li.', {
            title: 'Li xc thc',
            type: 'error'
          })
          // Xa token khng hp l
          localStorage.removeItem('admin_current_token')
          localStorage.removeItem('token')
          // Chuyn hng n trang ng nhp sau 3 giy
          setTimeout(() => {
            redirectToLogin()
          }, 3000)
        } else {
          notificationService.show(`Li t my ch: ${apiError.response.status} - ${apiError.response.data?.message || 'Khng th lu sn phm'}`, {
            title: 'Li',
            type: 'error'
          })
        }
      } else {
        notificationService.show('Khng th kt ni n my ch API. Vui lng kim tra kt ni mng.', {
          title: 'Li kt ni',
          type: 'error'
        })
      }
    }
  } catch (err) {
    console.error('Li chung khi lu sn phm:', err)
    notificationService.show(' xy ra li khi lu sn phm. Vui lng th li sau.', {
      title: 'Li',
      type: 'error'
    })
  } finally {
    isLoading.value = false
  }
}

// Delete product
const showDeleteConfirm = ref(false)
const productToDelete = ref(null)

function openDeleteConfirm(productId) {
  productToDelete.value = productId
  showDeleteConfirm.value = true
}

function handleDeleteConfirm() {
  const productId = productToDelete.value
  showDeleteConfirm.value = false
  if (productId) {
    deleteProduct(productId)
  }
}

async function deleteProduct(productId) {
  // Kim tra ID sn phm hp l
  if (!productId || isNaN(parseInt(productId)) || parseInt(productId) <= 0) {
    notificationService.show('ID sn phm khng hp l, vui lng th li.', {
      title: 'Li d liu',
      type: 'error'
    })
    return
  }
  
  try {
    // Kim tra token trc khi gi API
    const token = localStorage.getItem('admin_current_token') || localStorage.getItem('token')
    if (!token) {
      notificationService.show('Bn cha ng nhp hoc phin lm vic  ht hn. Vui lng ng nhp li.', {
        title: 'Li xc thc',
        type: 'error'
      })
      // T ng chuyn hng n trang ng nhp sau 3 giy
      setTimeout(() => {
        redirectToLogin()
      }, 3000)
      return
    }
    
    console.log(`ang gi yu cu xa sn phm ID: ${productId}`)
    isLoading.value = true
    
    try {
      // S dng API wrapper
      await api.product.deleteProduct(productId)
      
      console.log(` xa sn phm ID: ${productId} thnh cng`)
      
      // Remove from local array
      products.value = products.value.filter(p => p.id !== productId)
      
      notificationService.show('Sn phm  c xa thnh cng', {
        title: 'Thnh cng',
        type: 'success'
      })
    } catch (apiError) {
      console.error('Li khi gi API xa sn phm:', apiError)
      
      if (apiError.response) {
        const status = apiError.response.status
        const errorMessage = apiError.response.data?.message || 'Khng c thng tin chi tit'
        
        // X l theo m li c th
        switch (status) {
          case 400:
            notificationService.show(`ID sn phm khng hp l: ${errorMessage}`, {
              title: 'Li d liu',
              type: 'error'
            })
            break
            
          case 401:
            notificationService.show('Token khng hp l hoc  ht hn. Vui lng ng nhp li.', {
              title: 'Li xc thc',
              type: 'error'
            })
            // Xa token khng hp l
            localStorage.removeItem('admin_current_token')
            localStorage.removeItem('token')
            // Chuyn hng n trang ng nhp sau 3 giy
            setTimeout(() => {
              redirectToLogin()
            }, 3000)
            break
            
          case 403:
            notificationService.show('Bn khng c quyn xa sn phm. Vui lng lin h qun tr vin.', {
              title: 'Li phn quyn',
              type: 'error'
            })
            break
            
          case 404:
            notificationService.show(`Khng tm thy sn phm vi ID: ${productId}`, {
              title: 'Khng tm thy',
              type: 'error'
            })
            // Xa khi mng local v khng tn ti trn server
            products.value = products.value.filter(p => p.id !== productId)
            break
            
          default:
            notificationService.show(`Li t my ch: ${status} - ${errorMessage}`, {
              title: 'Li',
              type: 'error'
            })
        }
      } else {
        notificationService.show('Khng th kt ni n my ch API. Vui lng kim tra kt ni mng.', {
          title: 'Li kt ni',
          type: 'error'
        })
      }
    } finally {
      isLoading.value = false
    }
  } catch (err) {
    console.error('Li chung khi xa sn phm:', err)
    notificationService.show('Khng th xa sn phm. Vui lng th li sau.', {
      title: 'Li',
      type: 'error'
    })
    isLoading.value = false
  }
}

// Pagination functions
function goToPage(page) {
  currentPage.value = page
  fetchAllProducts()
}

function nextPage() {
  if (currentPage.value < totalPages.value - 1) {
    currentPage.value++
    fetchAllProducts()
  }
}

function prevPage() {
  if (currentPage.value > 0) {
    currentPage.value--
    fetchAllProducts()
  }
}

// Search and filter functions
function applySearch() {
  currentPage.value = 0
  fetchAllProducts()
}

function searchProducts() {
  applySearch()
}

function resetFilters() {
  searchTerm.value = ''
  selectedCategory.value = ''
  statusFilter.value = ''
  stockFilter.value = 'all'
  fetchAllProducts()
}

// Load data when component mounts
onMounted(async () => {
  try {
    // Ti danh mc sn phm
    await fetchCategories()
    
    // Ti tt c sn phm, bao gm c ACTIVE v INACTIVE
    await fetchAllProducts()
    
    console.log(' khi to trang sn phm vi', products.value.length, 'sn phm')
  } catch (error) {
    console.error('Li khi khi to trang:', error)
  }
})

// Cp nht nt xa sn phm  gi openDeleteConfirm
function confirmDeleteProduct(productId) {
  openDeleteConfirm(productId);
}

// Quick update functions
async function quickUpdateStatus(product) {
  if (isUpdating.value[product.id]) return
  
  isUpdating.value[product.id] = true
  
  try {
    const token = localStorage.getItem('admin_current_token') || localStorage.getItem('token')
    if (!token) {
      notificationService.show('Bn cn ng nhp li  thc hin thao tc ny', {
        title: 'Li xc thc',
        type: 'error'
      })
      return
    }
    
    // S dng API wrapper (ch gi trng status)
    await api.product.updateProduct(product.id, {
      status: product.status
    })
    
    console.log(` cp nht trng thi sn phm ID: ${product.id} thnh ${product.status}`)
    
    notificationService.show(` cp nht trng thi thnh ${getStatusDisplayText(product.status)}`, {
      title: 'Cp nht thnh cng',
      type: 'success',
      duration: 2000
    })
    
  } catch (error) {
    console.error('Li khi cp nht trng thi:', error)
    
    // X l li HTTP c th
    if (error.response) {
      const status = error.response.status
      
      handleApiError(status, error.response.data?.message, 'trng thi')
      
      // Khi phc trng thi ban u
      const originalProduct = products.value.find(p => p.id === product.id)
      if (originalProduct) {
        product.status = originalProduct.status
      }
    } else {
      notificationService.show('Li kt ni khi cp nht trng thi', {
        title: 'Li',
        type: 'error'
      })
    }
  } finally {
    isUpdating.value[product.id] = false
  }
}

async function quickUpdateStock(product) {
  if (isUpdating.value[product.id]) return
  
  isUpdating.value[product.id] = true
  
  try {
    const token = localStorage.getItem('admin_current_token') || localStorage.getItem('token')
    if (!token) {
      notificationService.show('Bn cn ng nhp li  thc hin thao tc ny', {
        title: 'Li xc thc',
        type: 'error'
      })
      return
    }
    
    // Validate stock
    if (product.stock < 0) {
      product.stock = 0
    }
    
    // S dng API wrapper (ch gi trng stock)
    await api.product.updateProduct(product.id, {
      stock: product.stock
    })
    
    console.log(` cp nht s lng sn phm ID: ${product.id} thnh ${product.stock}`)
    
    notificationService.show(` cp nht s lng thnh ${product.stock}`, {
      title: 'Cp nht thnh cng',
      type: 'success',
      duration: 2000
    })
    
  } catch (error) {
    console.error('Li khi cp nht s lng:', error)
    
    // X l li HTTP c th
    if (error.response) {
      const status = error.response.status
      
      handleApiError(status, error.response.data?.message, 's lng')
      
      // Khi phc s lng ban u
      const originalProduct = products.value.find(p => p.id === product.id)
      if (originalProduct) {
        product.stock = originalProduct.stock
      }
    } else {
      notificationService.show('Li kt ni khi cp nht s lng', {
        title: 'Li',
        type: 'error'
      })
    }
  } finally {
    isUpdating.value[product.id] = false
  }
}

// Hm x l li API chung
function handleApiError(status, message, fieldName) {
  switch (status) {
    case 400:
      notificationService.show(`D liu ${fieldName} khng hp l: ${message || 'Vui lng kim tra li'}`, {
        title: 'Li d liu',
        type: 'error'
      })
      break
    case 401:
      notificationService.show('Token khng hp l hoc  ht hn. Vui lng ng nhp li.', {
        title: 'Li xc thc',
        type: 'error'
      })
      localStorage.removeItem('admin_current_token')
      localStorage.removeItem('token')
      setTimeout(() => {
        redirectToLogin()
      }, 3000)
      break
    case 403:
      notificationService.show('Bn khng c quyn thc hin thao tc ny', {
        title: 'Li phn quyn',
        type: 'error'
      })
      break
    case 404:
      notificationService.show(`Khng tm thy sn phm cn cp nht ${fieldName}`, {
        title: 'Khng tm thy',
        type: 'error'
      })
      break
    default:
      notificationService.show(`Li my ch khi cp nht ${fieldName}: ${message || status}`, {
        title: 'Li',
        type: 'error'
      })
  }
}

// Hm ly CSS class da trn trng thi
function getStatusClass(status) {
  if (status === 'ACTIVE') {
    return 'bg-green-100 text-green-800 border-green-300 focus:ring-green-500'
  } else {
    return 'bg-gray-100 text-gray-800 border-gray-300 focus:ring-gray-500'
  }
}

// Hm ly text hin th cho trng thi
function getStatusDisplayText(status) {
  if (status === 'ACTIVE') {
    return 'ang bn'
  } else {
    return 'Khng bn'
  }
}

// Hm x l d liu sn phm trc khi hin th
function processProductData(productData) {
  // Khng cn x l chun ha trng thi na, tr v nguyn bn
  return productData;
}

const filterProducts = () => {
  // Reset to first page when filters change
  currentPage.value = 1
}

const filterByCategory = () => {
  filterProducts()
}
</script>

<style scoped>
