import { ref, computed } from 'vue'

// S dng localStorage  lu gi hng
const CART_STORAGE_KEY = 'sam-ngoc-linh-cart'

// To state v methods vi Composition API
const cartItems = ref(loadCartFromStorage() || [])

// Lu gi hng xung localStorage
function saveCartToStorage() {
  localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(cartItems.value))
}

// c gi hng t localStorage
function loadCartFromStorage() {
  const savedCart = localStorage.getItem(CART_STORAGE_KEY)
  return savedCart ? JSON.parse(savedCart) : []
}

// Tnh tng tin gi hng
const cartTotal = computed(() => {
  return cartItems.value.reduce((total, item) => total + (item.price * item.quantity), 0)
})

// S lng sn phm trong gi hng
const cartItemCount = computed(() => {
  return cartItems.value.reduce((count, item) => count + item.quantity, 0)
})

// Thm sn phm vo gi hng
function addToCart(product, quantity = 1) {
  const existingItem = cartItems.value.find(item => item.id === product.id)
  
  if (existingItem) {
    // Sn phm  c trong gi hng, tng s lng
    existingItem.quantity += quantity
  } else {
    // Thm sn phm mi vo gi hng
    cartItems.value.push({
      id: product.id,
      name: product.name,
      price: product.price,
      image: product.image,
      quantity: quantity
    })
  }
  
  saveCartToStorage()
}

// Cp nht s lng sn phm
function updateCartItemQuantity(productId, newQuantity) {
  const item = cartItems.value.find(item => item.id === productId)
  
  if (item) {
    if (newQuantity <= 0) {
      // Nu s lng  0, xa sn phm khi gi hng
      removeFromCart(productId)
    } else {
      item.quantity = newQuantity
      saveCartToStorage()
    }
  }
}

// Xa sn phm khi gi hng
function removeFromCart(productId) {
  const index = cartItems.value.findIndex(item => item.id === productId)
  
  if (index !== -1) {
    cartItems.value.splice(index, 1)
    saveCartToStorage()
  }
}

// Xa ton b gi hng
function clearCart() {
  cartItems.value = []
  saveCartToStorage()
}

export default {
  cartItems,
  cartTotal,
  cartItemCount,
  addToCart,
  updateCartItemQuantity,
  removeFromCart,
  clearCart
} 