import { ref, computed } from 'vue'
import sessionToken from './sessionToken'
import api from '@/api'

// To store qun l authentication
const auth = {
  isAuthenticated: ref(false),
  currentUser: ref(null),
  isLoading: ref(false),
  
  // Khi to store t localStorage nu  ng nhp trc 
  init() {
    try {
      // Kim tra token hin ti c hp l khng
      if (sessionToken.isCurrentTokenValid()) {
        const session = sessionToken.getCurrentSession()
        if (session) {
          // Khi phc thng tin ngi dng t session
          this.currentUser.value = {
            id: session.userId,
            name: session.userName,
            email: session.userEmail,
            role: session.userRole
          }
          this.isAuthenticated.value = true
        }
      } else {
        // Nu token khng hp l, ng xut
        this.logout(false) // false  khng xa token ( c xa trong sessionToken)
      }
    } catch (error) {
      console.error('Error initializing auth store:', error)
      // Reset auth state on error
      this.currentUser.value = null
      this.isAuthenticated.value = false
    }
  },
  
  // ng nhp
  async login({ email, password, rememberMe }) {
    this.isLoading.value = true
    
    try {
      // Gi API ng nhp
      const response = await api.auth.login({
        email,
        password
      })
      
      // Ly token t response
      const token = response.data
      
      // Gi lp d liu user t token
      // Thc t s decode token hoc gi API get profile
      const user = {
        id: 1, // Gi lp ID
        email: email,
        name: email.split('@')[0], // Ly tn t email 
        role: email.includes('admin') ? 'admin' : 'user'
      }
      
      // Lu thng tin ng nhp
      this.currentUser.value = user
      this.isAuthenticated.value = true
      
      // To token session
      sessionToken.createToken(user, rememberMe)
      
      return true
    } catch (error) {
      console.error('Login error:', error)
      return false
    } finally {
      this.isLoading.value = false
    }
  },
  
  // ng k
  async register({ name, email, password }) {
    this.isLoading.value = true
    
    try {
      // Gi API ng k
      await api.auth.register({
        name,
        email,
        password
      })
      
      // Tr v true khi ng k thnh cng
      return true
    } catch (error) {
      console.error('Register error:', error)
      return false
    } finally {
      this.isLoading.value = false
    }
  },
  
  // ng xut
  async logout(removeToken = true) {
    this.isLoading.value = true
    
    try {
      // Gi API ng xut nu  ng nhp v cn xa token
      if (this.isAuthenticated.value && removeToken) {
        try {
          await api.auth.logout()
        } catch (error) {
          console.error('Error calling logout API:', error)
          // Tip tc qu trnh logout d API c li
        }
      }
      
      // Xa d liu ngi dng
      this.currentUser.value = null
      this.isAuthenticated.value = false
      
      // Xa token session nu cn
      if (removeToken) {
        sessionToken.removeToken(sessionToken.currentToken.value)
      }
      
      return true
    } catch (error) {
      console.error('Logout error:', error)
      return false
    } finally {
      this.isLoading.value = false
    }
  },
  
  // Kim tra quyn admin
  isAdmin: computed(() => {
    if (!auth.isAuthenticated.value) return false
    return auth.currentUser.value?.role === 'admin'
  }),
  
  // Cp nht hot ng ca phin hin ti
  updateActivity() {
    if (this.isAuthenticated.value) {
      sessionToken.updateActivity()
    }
  },
  
  // Ly danh sch phin ng nhp ca ngi dng hin ti
  getUserSessions: computed(() => {
    if (!auth.isAuthenticated.value) return []
    return sessionToken.getCurrentUserSessions.value
  }),
  
  // Xa mt phin ng nhp c th
  removeSession(tokenId) {
    if (this.isAuthenticated.value) {
      // Nu xa phin hin ti, thc hin ng xut
      if (tokenId === sessionToken.currentToken.value) {
        this.logout()
      } else {
        sessionToken.removeToken(tokenId)
      }
    }
  },
  
  // Xa tt c phin ng nhp ngoi tr phin hin ti
  removeOtherSessions() {
    if (this.isAuthenticated.value) {
      sessionToken.removeOtherTokens()
    }
  }
}

// Khi to store khi import
auth.init()

export default auth 