import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import ProductDetail from '@/views/ProductDetail.vue'
import Cart from '@/views/Cart.vue'
import Checkout from '@/views/Checkout.vue'
import Products from '@/views/Products.vue'
import About from '@/views/About.vue'
import Services from '@/views/Services.vue'
import News from '@/views/News.vue'
import Contact from '@/views/Contact.vue'
import Login from '@/views/admin/Login.vue'
import Register from '@/views/admin/Register.vue'
import Dashboard from '@/views/admin/Dashboard.vue'
import AdminProducts from '@/views/admin/Products.vue'
import AdminServices from '@/views/admin/Services.vue'
import Users from '@/views/admin/Users.vue'
import AdminNews from '@/views/admin/News.vue'
import Categories from '@/views/admin/Categories.vue'
import Orders from '@/views/admin/Orders.vue'
import Sessions from '@/views/admin/Sessions.vue'
import auth from '@/store/auth'
import sessionToken from '@/store/sessionToken'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { requiresAuth: false }
  },
  {
    path: '/products',
    name: 'Products',
    component: Products,
    meta: { requiresAuth: false }
  },
  {
    path: '/product/:id',
    name: 'ProductDetail',
    component: ProductDetail,
    props: true,
    meta: { requiresAuth: false }
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart,
    meta: { requiresAuth: false }
  },
  {
    path: '/checkout',
    name: 'Checkout',
    component: Checkout,
    meta: { requiresAuth: false }
  },
  {
    path: '/about',
    name: 'About',
    component: About,
    meta: { requiresAuth: false }
  },
  {
    path: '/services',
    name: 'Services',
    component: Services,
    meta: { requiresAuth: false }
  },
  {
    path: '/news',
    name: 'News',
    component: News,
    meta: { requiresAuth: false }
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact,
    meta: { requiresAuth: false }
  },
  {
    path: '/admin/login',
    name: 'AdminLogin',
    component: Login,
    meta: { requiresGuest: true }
  },
  {
    path: '/admin/register',
    name: 'AdminRegister',
    component: Register,
    meta: { requiresGuest: true }
  },
  {
    path: '/admin',
    redirect: '/admin/dashboard',
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/dashboard',
    name: 'AdminDashboard',
    component: Dashboard,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/products',
    name: 'AdminProducts',
    component: AdminProducts,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/services',
    name: 'AdminServices',
    component: AdminServices,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/categories',
    name: 'AdminCategories',
    component: Categories,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/users',
    name: 'AdminUsers',
    component: Users,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/news',
    name: 'AdminNews',
    component: AdminNews,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/orders',
    name: 'AdminOrders',
    component: Orders,
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/admin/sessions',
    name: 'AdminSessions',
    component: Sessions,
    meta: { requiresAuth: true, requiresAdmin: true }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior() {
    // Lun cun ln u trang khi chuyn route
    return { top: 0 }
  }
})

// Navigation Guards
router.beforeEach((to, from, next) => {
  // Cp nht hot ng khi chuyn trang nu  ng nhp
  if (auth.isAuthenticated.value) {
    auth.updateActivity()
  }
  
  // Kim tra nu route yu cu xc thc
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // Kim tra token
    const token = localStorage.getItem('admin_current_token')
    
    if (!token) {
      // Khng c token, chuyn hng n trang ng nhp
      next({
        path: '/admin/login',
        query: { redirect: to.fullPath }
      })
    } else {
      // C token, cho php truy cp
      next()
    }
  } else {
    // Route khng yu cu xc thc
    next()
  }
})

// Cp nht meta cho cc route admin  yu cu xc thc
const adminRoutes = router.getRoutes().filter(route => 
  route.path.startsWith('/admin') && 
  !route.path.includes('/login') && 
  !route.path.includes('/register')
)

adminRoutes.forEach(route => {
  route.meta.requiresAuth = true
})

export default router 