<script setup>
import { ref, onMounted } from 'vue'
import AOS from 'aos'
import api from '@/api'
import cart from '@/store/cart'
import { useRouter } from 'vue-router'

// Initialize router
const router = useRouter()

// Fix image path by using public path instead of relative import
const samTuoiImage = '/assets/images/products/sam-tuoi.png'

// State for products
const featuredProducts = ref([])
const isLoading = ref(true)
const error = ref(null)

// Format gi tin
function formatPrice(price) {
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND'
  }).format(price).replace('', 'VN')
}

// Fetch featured products from API
async function fetchFeaturedProducts() {
  isLoading.value = true
  error.value = null
  
  try {
    // Fetch products with limit=3 to get only featured ones
    const response = await api.product.getProducts({
      page: 0,
      size: 3,
      sort: 'sold',
      direction: 'desc'
    })
    
    // Map API response to our format - updated to match actual API structure
    featuredProducts.value = response.data.products.map(product => ({
      id: product.id,
      name: product.name,
      description: product.description,
      price: product.price,
      image: product.image ? (product.image.startsWith('http') ? product.image : `${import.meta.env.VITE_API_URL}${product.image}`) : samTuoiImage,
      slug: product.slug
    }))
  } catch (err) {
    console.error('Error fetching products:', err)
    error.value = 'Khng th ti sn phm. Vui lng th li sau.'
    
    // Fallback to sample data if API fails
    featuredProducts.value = [
      {
        id: 1,
        name: 'Sm Ngc Linh ti 10 nm tui',
        description: 'Sm ngc linh ti c tui i 10 nm, c thu hoch t vng ni Ngc Linh, gi nguyn dc tnh qu gi.',
        price: 2500000,
        image: samTuoiImage,
        slug: 'sam-ngoc-linh-tuoi-10-nam-tuoi'
      },
      {
        id: 2,
        name: 'Cao Sm Ngc Linh',
        description: 'Cao sm ngc linh nguyn cht, tinh khit, gip bi b sc khe hiu qu, tng cng h min dch.',
        price: 1800000,
        image: samTuoiImage,
        slug: 'cao-sam-ngoc-linh'
      },
      {
        id: 3,
        name: 'Ru Sm Ngc Linh',
        description: 'Ru ngm sm ngc linh, gip tng cng sinh lc v bi b c th, ph hp cho ngi ln tui.',
        price: 1500000,
        image: samTuoiImage,
        slug: 'ruou-sam-ngoc-linh'
      }
    ]
  } finally {
    isLoading.value = false
  }
}

// Handle add to cart functionality
function addToCart(product) {
  cart.addToCart(product, 1)
  alert(` thm ${product.name} vo gi hng!`)
}

// Handle buy now functionality
function buyNow(product) {
  cart.addToCart(product, 1)
  alert(`ang chuyn n trang thanh ton cho sn phm: ${product.name}`)
  router.push('/checkout')
}

// Fetch data and initialize AOS on component mount
onMounted(() => {
  fetchFeaturedProducts()
  AOS.refresh()
})
</script>

