<template>
  <div class="admin-register min-h-screen bg-gray-100 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
    <div class="max-w-md w-full space-y-8 bg-white p-8 rounded-xl shadow-md">
      <div class="text-center">
        <div class="flex justify-center">
          <div class="w-16 h-16 bg-green-100 rounded-full overflow-hidden flex-shrink-0">
            <img :src="samTuoiImage" alt="Sm Ngc Linh" class="w-full h-full object-cover">
          </div>
        </div>
        <h2 class="mt-6 text-3xl font-extrabold text-gray-900">ng k ti khon</h2>
        <p class="mt-2 text-sm text-gray-600">
          Hoc
          <router-link to="/admin/login" class="font-medium text-green-600 hover:text-green-500">
            ng nhp nu  c ti khon
          </router-link>
        </p>
      </div>
      
      <div v-if="errorMessage" class="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-4" role="alert">
        <p>{{ errorMessage }}</p>
      </div>
      
      <div v-if="successMessage" class="bg-green-100 border-l-4 border-green-500 text-green-700 p-4 mb-4" role="alert">
        <p>{{ successMessage }}</p>
      </div>
      
      <form class="mt-8 space-y-6" @submit.prevent="register">
        <div class="rounded-md -space-y-px">
          <div class="mb-4">
            <label for="name" class="block text-sm font-medium text-gray-700">H tn</label>
            <input 
              id="name" 
              v-model="form.name"
              name="name" 
              type="text" 
              autocomplete="name" 
              required 
              class="mt-1 appearance-none relative block w-full px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-500 focus:border-green-500 focus:z-10 sm:text-sm"
              placeholder="Nguyn Vn A"
            >
          </div>
          
          <div class="mb-4">
            <label for="email" class="block text-sm font-medium text-gray-700">Email</label>
            <input 
              id="email" 
              v-model="form.email"
              name="email" 
              type="email" 
              autocomplete="email" 
              required 
              class="mt-1 appearance-none relative block w-full px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-500 focus:border-green-500 focus:z-10 sm:text-sm"
              placeholder="example@email.com"
            >
          </div>
          
          <div class="mb-4">
            <label for="password" class="block text-sm font-medium text-gray-700">Mt khu</label>
            <input 
              id="password" 
              v-model="form.password"
              name="password" 
              type="password" 
              autocomplete="new-password" 
              required 
              class="mt-1 appearance-none relative block w-full px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-500 focus:border-green-500 focus:z-10 sm:text-sm"
              placeholder=""
            >
          </div>
          
          <div>
            <label for="password_confirm" class="block text-sm font-medium text-gray-700">Xc nhn mt khu</label>
            <input 
              id="password_confirm" 
              v-model="form.passwordConfirm"
              name="password_confirm" 
              type="password" 
              autocomplete="new-password" 
              required 
              class="mt-1 appearance-none relative block w-full px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-500 focus:border-green-500 focus:z-10 sm:text-sm"
              placeholder=""
            >
          </div>
        </div>

        <div>
          <div class="flex items-center">
            <input 
              id="agree-terms" 
              v-model="agreeTerms"
              name="agree-terms" 
              type="checkbox" 
              required
              class="h-4 w-4 text-green-600 focus:ring-green-500 border-gray-300 rounded"
            >
            <label for="agree-terms" class="ml-2 block text-sm text-gray-900">
              Ti ng  vi cc <a href="#" class="text-green-600 hover:text-green-500">iu khon v iu kin</a>
            </label>
          </div>
        </div>

        <div>
          <button 
            type="submit"
            :disabled="isLoading"
            class="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 disabled:opacity-75"
          >
            <span v-if="isLoading" class="absolute left-0 inset-y-0 flex items-center pl-3">
              <i class="fas fa-spinner fa-spin"></i>
            </span>
            <span v-else class="absolute left-0 inset-y-0 flex items-center pl-3">
              <i class="fas fa-user-plus"></i>
            </span>
            {{ isLoading ? 'ang x l...' : 'ng k' }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import api from '@/api'
import auth from '@/store/auth'

const router = useRouter()
const errorMessage = ref('')
const isLoading = ref(false)
const successMessage = ref('')
const agreeTerms = ref(false)

const samTuoiImage = '/assets/images/products/sam-tuoi.png'

// Form data
const form = reactive({
  name: '',
  email: '',
  password: '',
  passwordConfirm: ''
})

// Validate form
function validateForm() {
  if (!form.name || !form.email || !form.password || !form.passwordConfirm) {
    errorMessage.value = 'Vui lng in y  thng tin'
    return false
  }
  
  if (form.password !== form.passwordConfirm) {
    errorMessage.value = 'Mt khu xc nhn khng khp'
    return false
  }
  
  if (form.password.length < 6) {
    errorMessage.value = 'Mt khu phi c t nht 6 k t'
    return false
  }
  
  return true
}

// Register function
async function register() {
  if (!validateForm()) return
  
  errorMessage.value = ''
  isLoading.value = true
  
  try {
    // Gi API ng k
    await api.auth.register({
      name: form.name,
      email: form.email,
      password: form.password
    })
    
    // Chuyn hng n trang ng nhp
    router.push({
      path: '/admin/login',
      query: { registered: 'success' }
    })
  } catch (error) {
    console.error('Register error:', error)
    
    // Hin th thng bo li ph hp
    if (error.response) {
      // Nu server tr v li
      switch (error.response.status) {
        case 409:
          errorMessage.value = 'Email  c s dng, vui lng chn email khc'
          break
        case 400:
          errorMessage.value = 'Thng tin khng hp l, vui lng kim tra li'
          break
        default:
          errorMessage.value = ' xy ra li, vui lng th li sau'
      }
    } else {
      // Li kt ni hoc li khc
      errorMessage.value = 'Khng th kt ni n my ch, vui lng th li sau'
    }
  } finally {
    isLoading.value = false
  }
}
</script> 