<template>
  <header class="bg-white shadow">
    <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div class="flex justify-between h-16">
        <!-- Logo v tn h thng -->
        <div class="flex items-center">
          <router-link to="/" class="flex items-center">
            <div class="w-10 h-10 bg-green-100 rounded-full overflow-hidden mr-2">
              <img src="/assets/images/products/sam-tuoi.png" alt="Sm Ngc Linh" class="w-full h-full object-cover">
            </div>
            <span class="text-xl font-semibold text-gray-900">Sm Ngc Linh</span>
          </router-link>
        </div>
        
        <!-- Menu chnh -->
        <nav class="hidden md:flex items-center space-x-6">
          <router-link 
            v-for="item in mainMenu" 
            :key="item.path"
            :to="item.path"
            class="text-sm font-medium transition-colors"
            :class="[
              isActive(item.path) 
                ? 'text-green-600' 
                : 'text-gray-500 hover:text-gray-900'
            ]"
          >
            {{ item.label }}
          </router-link>
        </nav>
        
        <!-- Gi hng v tc v khc -->
        <div class="flex items-center space-x-4">
          <!-- Gi hng -->
          <router-link to="/cart" class="p-2 text-gray-500 hover:text-gray-900 relative">
            <i class="fas fa-shopping-cart" :class="{ 'animate-cart-bounce': showCartAnimation }"></i>
            <span v-if="cartItemCount > 0" class="absolute top-0 right-0 bg-green-500 text-white text-xs rounded-full h-4 w-4 flex items-center justify-center"
                  :class="{ 'animate-pulse': showCartAnimation }">
              {{ cartItemCount }}
            </span>
          </router-link>
          
          <!-- Tm kim -->
          <button @click="toggleSearch" class="p-2 text-gray-500 hover:text-gray-900">
            <i class="fas fa-search"></i>
          </button>
          
          <!-- Menu mobile -->
          <button @click="toggleMobileMenu" class="p-2 text-gray-500 hover:text-gray-900 md:hidden">
            <i :class="isMobileMenuOpen ? 'fas fa-times' : 'fas fa-bars'"></i>
          </button>
        </div>
      </div>
    </div>
    
    <!-- Thanh tm kim -->
    <div v-if="isSearchOpen" class="border-t border-gray-200 py-3">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <form @submit.prevent="submitSearch" class="flex">
          <input 
            v-model="searchQuery"
            type="text" 
            placeholder="Tm kim sn phm..." 
            class="flex-grow px-4 py-2 border border-gray-300 focus:ring-green-500 focus:border-green-500 rounded-l-md"
          >
          <button 
            type="submit" 
            class="bg-green-600 text-white px-4 py-2 rounded-r-md hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500"
          >
            <i class="fas fa-search"></i>
          </button>
        </form>
      </div>
    </div>
    
    <!-- Menu mobile -->
    <div v-if="isMobileMenuOpen" class="md:hidden border-t border-gray-200">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-3">
        <div class="space-y-1">
          <router-link 
            v-for="item in mainMenu" 
            :key="item.path"
            :to="item.path"
            class="block py-2 text-base font-medium transition-colors"
            :class="[
              isActive(item.path) 
                ? 'text-green-600' 
                : 'text-gray-500 hover:text-gray-900'
            ]"
            @click="closeMobileMenu"
          >
            {{ item.label }}
          </router-link>
        </div>
      </div>
    </div>
  </header>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import cart from '@/store/cart'
import eventBus from '@/utils/eventBus'

const route = useRoute()
const router = useRouter()

// State
const isMobileMenuOpen = ref(false)
const isSearchOpen = ref(false)
const searchQuery = ref('')
const cartItemCount = computed(() => cart.cartItemCount.value)
const showCartAnimation = ref(false)

// Menu chnh
const mainMenu = [
  { label: 'Trang ch', path: '/' },
  { label: 'Sn phm', path: '/products' },
  { label: 'Dch v', path: '/services' },
  { label: 'Tin tc', path: '/news' },
  { label: 'Gii thiu', path: '/about' },
  { label: 'Lin h', path: '/contact' }
]

// Kim tra route hin ti
function isActive(path) {
  if (path === '/') {
    return route.path === '/'
  }
  return route.path.startsWith(path)
}

// M/ng menu mobile
function toggleMobileMenu() {
  isMobileMenuOpen.value = !isMobileMenuOpen.value
  if (isMobileMenuOpen.value) {
    isSearchOpen.value = false
  }
}

// ng menu mobile
function closeMobileMenu() {
  isMobileMenuOpen.value = false
}

// M/ng thanh tm kim
function toggleSearch() {
  isSearchOpen.value = !isSearchOpen.value
  if (isSearchOpen.value) {
    isMobileMenuOpen.value = false
    // Focus vo input tm kim
    setTimeout(() => {
      document.querySelector('input[type="text"]')?.focus()
    }, 100)
  }
}

// X l tm kim
function submitSearch() {
  if (searchQuery.value.trim()) {
    router.push({
      path: '/products',
      query: { search: searchQuery.value }
    })
    isSearchOpen.value = false
  }
}

// Lng nghe s kin khi sn phm c thm vo gi hng
function onProductAddedToCart() {
  showCartAnimation.value = true
  
  // Tt animation sau 1.5 giy
  setTimeout(() => {
    showCartAnimation.value = false
  }, 1500)
}

onMounted(() => {
  eventBus.on('product-added-to-cart', onProductAddedToCart)
})

onUnmounted(() => {
  eventBus.off('product-added-to-cart', onProductAddedToCart)
})
</script>

<style scoped>
@keyframes cart-bounce {
  0%, 100% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-4px) rotate(-10deg);
  }
  50% {
    transform: translateY(0) rotate(0deg);
  }
  75% {
    transform: translateY(-2px) rotate(5deg);
  }
}

.animate-cart-bounce {
  animation: cart-bounce 0.8s ease;
}

.animate-pulse {
  animation: pulse 1.5s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
</style> 